body {
  color: #333;
  font-size: 20px;
  line-height: 1.5;
  font-family: "SF Pro Display";
}
@media (max-width: 767.9px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 575.9px) {
  body {
    font-size: 15px;
  }
}

html {
  scroll-behavior: smooth;
}

::selection {
  background-color: rgba(216, 17, 70, 0.7);
  color: #fff;
}

body.loading {
  opacity: 0.4;
  pointer-events: none;
  cursor: auto;
  user-select: none;
}

a,
a:visited,
a:active {
  color: #000;
  text-decoration: none;
  transition: 0.15s;
}

iframe {
  border: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

p {
  margin: 0;
  margin-top: 15px;
}

.hov-primary:hover {
  color: #d81146;
}

.hov-opacity:hover {
  opacity: 0.6;
}

a:focus, a:active,
button:focus,
button:active {
  outline: none;
}

.animate-up div {
  overflow: hidden;
}
.animate-up span {
  display: inline-block;
  transition: transform 0.3s cubic-bezier(0.5, 1, 0.89, 1);
  transform: translateY(100%);
}
.animate-up div:nth-child(2) span {
  transition-delay: 0.25s;
}
.animate-up div:nth-child(3) span {
  transition-delay: 0.3s;
}
.animate-up div:nth-child(4) span {
  transition-delay: 0.35s;
}
.animate-up div:nth-child(5) span {
  transition-delay: 0.4s;
}
.animate-up div:nth-child(6) span {
  transition-delay: 0.45s;
}
.animate-up.in-viewport span {
  transform: none;
}

.video-wrap {
  position: relative;
  overflow: hidden;
}

.site-wrap {
  overflow: hidden;
}

.video-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: 0.5s;
}
.video-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-cover svg {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 5;
  transform: translate(-50%, -50%);
  transition: 0.2s;
}
.video-cover:hover svg {
  transform: translate(-50%, -50%) scale(1.2);
}
.video-cover:active svg {
  transform: translate(-50%, -50%);
}
.loading .video-cover {
  cursor: auto;
  pointer-events: none;
}
.loading .video-cover svg {
  transform: translate(-50%, -50%) scale(1.2);
}
.loaded .video-cover {
  pointer-events: none;
  user-select: none;
  opacity: 0;
}

@media (max-width: 991.9px) {
  .hide-md {
    display: none !important;
  }
}

@media (min-width: 992px) {
  .display-md {
    display: none !important;
  }
}

.hamburger {
  position: relative;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 3px;
}
.hamburger .hamburger-els {
  display: block;
  position: relative;
  width: 100%;
  height: 0px;
  overflow: visible;
}
.hamburger .hamburger-els:after, .hamburger .hamburger-els:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #333;
  transition: 0.1s;
}
.hamburger .hamburger-els:after {
  top: 3px;
}
.hamburger .hamburger-els:before {
  bottom: 3px;
}

.mobile-menu-is-open .js-open-mobile-menu .hamburger-els:after {
  top: -1px;
  transform: rotate(45deg);
}
.mobile-menu-is-open .js-open-mobile-menu .hamburger-els:before {
  bottom: -1px;
  transform: rotate(-45deg);
}

body.mobile-menu-is-open {
  overflow: hidden;
}

