.btn-row {
  display: flex;
  flex-wrap: wrap;
}

.btn,
a.btn,
button.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-shrink: 0;
  padding: 15px 20px;
  border: none;
  transition: 0.2s;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
}
.btn.btn-primary,
a.btn.btn-primary,
button.btn.btn-primary {
  background-color: #d81146;
  color: #fff;
}
.btn.btn-primary:hover,
a.btn.btn-primary:hover,
button.btn.btn-primary:hover {
  background-color: #fff;
  color: #d81146;
}
.btn.btn-hov-primary,
a.btn.btn-hov-primary,
button.btn.btn-hov-primary {
  background-color: #d81146;
  color: #fff;
}
.btn.btn-hov-primary:hover,
a.btn.btn-hov-primary:hover,
button.btn.btn-hov-primary:hover {
  background-color: #ba0028;
  color: #fff;
}
.btn.btn-border,
a.btn.btn-border,
button.btn.btn-border {
  border: 2px solid rgba(216, 17, 70, 0.3);
  color: #d81146;
}
.btn.btn-border:hover,
a.btn.btn-border:hover,
button.btn.btn-border:hover {
  background-color: #d81146;
  color: #fff;
  border-color: #d81146;
}
.btn.btn-border-fff,
a.btn.btn-border-fff,
button.btn.btn-border-fff {
  border: 2px solid #fff;
  color: #fff;
}
.btn.btn-border-fff:hover,
a.btn.btn-border-fff:hover,
button.btn.btn-border-fff:hover {
  background-color: #fff;
  color: #d81146;
}

.btn:disabled,
a.btn:disabled {
  pointer-events: none !important;
  cursor: auto !important;
}

