.accordion-item:not(.active) .accordion-body {
  display: none;
}

.accordion-head {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.accordion-item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.accordion-item:last-child {
  border-bottom: 0;
}

.accordion-ico {
  display: block;
  margin-left: auto;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  position: relative;
  margin-left: 20px;
  margin-left: auto;
}
.accordion-ico:after, .accordion-ico:before {
  content: "";
  position: absolute;
  display: block;
  background-color: #000;
}
.accordion-ico:after {
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  transform: translateY(-50%);
}
.accordion-ico:before {
  top: 0;
  left: 50%;
  height: 100%;
  width: 2px;
  transform: translateX(-50%);
  transition: 0.2s;
}

.accordion-item.active .accordion-ico:before {
  transform: translateX(-50%) rotateX(90deg);
}

.accordion-body {
  padding-bottom: 30px;
