  justify-content: space-between;
}
@media (max-width: 767.9px) {
  .footer {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .footer .container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .footer .sign-col {
    padding-top: 30px;
    text-align: center;
  }
}

/*==============================================================
*												MOBILE MENU
=============================================================== */
.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  transition: 0.2s;
  transform: translateX(100%);
  opacity: 0;
  padding-top: 90px;
  padding-bottom: 30px;
}
.mobile-menu .container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.mobile-menu .btn-row {
  margin-top: auto;
  padding-top: 30px;
}
.mobile-menu-is-open .mobile-menu {
  transform: none;
  opacity: 1;
  z-index: 10;
}
.mobile-menu .menu-glavnoe-container {
  margin-top: auto;
  margin-bottom: auto;
}
.mobile-menu .menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
.mobile-menu .menu-item a {
  font-size: 25px;
  font-weight: bold;
}
.mobile-menu .menu-item a:hover {
  color: #d81146;
}
.mobile-menu .menu-item:not(:last-child) {
  margin-bottom: 30px;
}
@media (max-width: 575.9px) {
  .mobile-menu .btn {
    width: 100%;
  }
}